//引入顶部导航
import actionbar from "@/components/actionbar/actionbar.vue"
//这是购买订单页面
const app = getApp();
export default {
	data() {
		return {
			//这是首页的高度
			statusBar:0,
			modalName: null,
			radio: 'radio0',
			gid:"",
			img:"",
			nums:0,
			storename:"",
			price:0,
			goodtitle:"",
			data:[],
			way:0,
			o_from:0,
			value:"",
			list:[
				"微信",
				"支付宝",
				"余额"
			]
		}
	},
	methods: {
		priceorder(){
			//为了小程序考虑只能这样写了
			uni.getStorage({
				key:"bindtokey",
				success:(res)=>{
					 // console.log(res.data,this.gid,this.nums,this.data,this.o_from,this.value)
					if(this.way==1){
						console.log("从pc")
						uni.request({
							url:"http://hbk.huiboke.com/api/order/createUnPayOrderInfo",
							method:"POST",
							data:{
								token:res.data,
								gid:this.gid,
								spec:this.data,
								quantity:this.nums,
								o_from:this.o_from,
								address_id:52,
								p_msg:this.value
							},
							success(reslove) {
								// console.log(reslove.data.code)
								if(reslove.data.code==0){
									console.log(reslove)
								}else{//如果成功以后弹出提示框
									console.log(111)
								}
							}
						})
					}else if(this.way==2){
						console.log("从手机过来的过来的")
					}else{
						console.log("从小程序过来的")
					}
				}
			})
		},
		RadioChange(e) {
			this.radio = e.detail.value
			// console.log(e.detail.value)
			// console.log(this.radio)
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		Determinepayment(){
			console.log(this.radio)
			
		}
	},
	onLoad(opction){
		// console.log(opction)
		let {gid,num,way,img,storename,price,goodtitle} = opction
		this.gid = gid
		this.way = way
		this.img = JSON.parse(img)
		this.nums = num
		this.storename = storename
		this.price = price
		this.goodtitle = goodtitle
		//使用eval方法 将字符串数组 转换为 真数组
		this.data = eval(opction.specname)







			this.o_from = 3

		this.statusBar = app.globalData.statusBar
	},
	components:{
		actionbar,
	}
}
